import { movieFieldList, MovieFieldNames } from './fields/movies';
import { seriesFieldList, SeriesFieldNames } from './fields/series';
import { episodesFieldList, EpisodeFieldNames, } from './fields/episodes';
export const isMovie = (entry) => !!entry.movieName;
export const isSeries = (entry) => !!entry.seriesName && !entry.seriesSeason;
export const isEpisode = (entry) => !!(entry.seriesSeason && entry.seriesEpisode && entry.seriesName);
const getPropFn = (mapping) => {
    const mapByField = mapping.reduce((res, item) => Object.entries(item).reduce((out, [k, v]) => ({
        ...out,
        [k]: [...(out[k] ?? []), v],
    }), res), {});
    return (entry, field) => (mapByField[field] ?? []).reduce((res, val) => res ?? entry[val], undefined);
};
const makeGetters = (names, mapping) => {
    const getProp = getPropFn(mapping);
    return (entry) => Object.values(names).reduce((obj, key) => ({
        ...obj,
        get [key]() {
            return getProp(entry, key);
        },
    }), {});
};
const getMovieProps = makeGetters(MovieFieldNames, movieFieldList);
const getSeriesProps = makeGetters(SeriesFieldNames, seriesFieldList);
const getEpisodeProps = makeGetters(EpisodeFieldNames, episodesFieldList);
export const toMovieEntry = (entry) => {
    return {
        ...entry,
        ...getMovieProps(entry),
    };
};
export const toSeriesEntry = (entry) => ({
    ...entry,
    ...getSeriesProps(entry),
});
export const toEpisodeEntry = (entry) => ({
    ...entry,
    ...getEpisodeProps(entry),
});
export const toDefaultEntry = (entry) => ({
    ...entry,
});
