import React, { useState, useCallback } from 'react';
import { MoreVert } from '@material-ui/icons';
import { IconButton, Menu, MenuItem } from '@material-ui/core';
const LinkDropdown = ({ options }) => {
    const [anchorEl, setAnchorEl] = useState();
    const handleClick = useCallback((event) => {
        event.stopPropagation();
        setAnchorEl(event.currentTarget);
    }, []);
    const handleClose = useCallback(() => setAnchorEl(undefined), []);
    const handleMenuClick = useCallback((event) => event.stopPropagation(), []);
    return (<>
      <IconButton edge="end" onClick={handleClick}>
        <MoreVert />
      </IconButton>
      <Menu anchorEl={anchorEl} open={!!anchorEl} onClose={handleClose}>
        {options.map(({ label, url }) => !!url && (<MenuItem key={url} component="a" href={url} target="_blank" rel="noreferrer noopener" onClick={handleMenuClick}>
                {label}
              </MenuItem>))}
      </Menu>
    </>);
};
export default LinkDropdown;
