import { useState, useEffect, useMemo } from 'react';
import { stringify } from 'qs';
import { snakeCase } from 'utils/fetch';
import { useFlexgetAPI } from 'core/api';
import { toSeriesEntry } from 'core/entry/utils';
import { TVDBFields, TVMazeFields, TraktFields, } from '../fields/series';
const tvdbToFields = (series) => ({
    seriesName: series.seriesName,
    [TVDBFields.Genres]: series.genres,
    [TVDBFields.Posters]: series.posters,
    [TVDBFields.Backdrops]: series.banner,
    [TVDBFields.Rating]: series.rating,
    [TVDBFields.Description]: series.overview,
    [TVDBFields.Url]: `http://thetvdb.com/index.php?tab=series&id=${series.tvdbId}`,
    [TVDBFields.ContentRating]: series.contentRating,
    [TVDBFields.Network]: series.network,
    [TVDBFields.Runtime]: series.runtime,
    [TVDBFields.ID]: series.tvdbId,
});
export const useTVDBLookup = (titleOrId, options) => {
    const query = stringify(snakeCase(options));
    const [entry, setEntry] = useState();
    const [state, request] = useFlexgetAPI(`/tvdb/series/${titleOrId}?${query}`);
    useEffect(() => {
        const fn = async () => {
            const resp = await request();
            if (resp.ok) {
                setEntry(tvdbToFields(resp.data));
            }
            return resp;
        };
        fn();
    }, [request]);
    return { ...state, entry };
};
const tvMazeToFields = (series) => ({
    seriesName: series.name,
    seriesYear: series.year,
    [TVMazeFields.Genres]: series.genres,
    [TVMazeFields.Posters]: series.originalImage,
    [TVMazeFields.Rating]: series.rating,
    [TVMazeFields.Description]: series.summary,
    [TVMazeFields.Url]: series.url,
    [TVMazeFields.Network]: series.network,
    [TVMazeFields.Runtime]: series.runtime,
    [TVDBFields.ID]: series.tvdbId,
    [TVMazeFields.ID]: series.tvmazeId,
});
export const useTVMazeLookup = (titleOrId) => {
    const [entry, setEntry] = useState();
    const [state, request] = useFlexgetAPI(`/tvmaze/series/${titleOrId}`);
    useEffect(() => {
        const fn = async () => {
            const resp = await request();
            if (resp.ok) {
                setEntry(tvMazeToFields(resp.data));
            }
            return resp;
        };
        fn();
    }, [request]);
    return { ...state, entry };
};
const traktToFields = (series) => ({
    seriesName: series.title,
    seriesYear: series.year,
    [TraktFields.Genres]: series.genres,
    [TraktFields.Rating]: series.rating,
    [TraktFields.Description]: series.overview,
    [TraktFields.Votes]: series.votes,
    [TraktFields.Url]: `https://trakt.tv/shows/${series.slug}`,
    [TraktFields.ContentRating]: series.certification,
    [TraktFields.Network]: series.network,
    [TraktFields.Runtime]: series.runtime,
    [TraktFields.ID]: series.id,
    [TVDBFields.ID]: series.tvdbId,
});
export const useTraktLookup = (options) => {
    const query = stringify(snakeCase(options));
    const [entry, setEntry] = useState();
    const [state, request] = useFlexgetAPI(`/trakt/series/?${query}`);
    useEffect(() => {
        const fn = async () => {
            const resp = await request();
            if (resp.ok) {
                setEntry(traktToFields(resp.data));
            }
            return resp;
        };
        fn();
    }, [request]);
    return { ...state, entry };
};
export const useSeriesLookup = (series) => {
    const { loading: tvdbLoading, entry: tvdbEntry } = useTVDBLookup(series[TVDBFields.ID] ?? series.seriesName, {});
    const { loading: traktLoading, entry: traktEntry } = useTraktLookup({
        title: series.seriesName,
        traktId: series[TraktFields.ID],
    });
    const { loading: tvMazeLoading, entry: tvMazeEntry } = useTVMazeLookup(series[TVMazeFields.ID] ?? series.seriesName);
    const entry = useMemo(() => toSeriesEntry({
        ...series,
        ...(traktEntry ?? {}),
        ...(tvdbEntry ?? {}),
        ...(tvMazeEntry ?? {}),
    }), [series, traktEntry, tvMazeEntry, tvdbEntry]);
    const loading = tvdbLoading || traktLoading || tvMazeLoading;
    return { entry, loading };
};
